/**
 * Main script for converting Pikachu Volleyball replay files to video
 * OFFLINE RENDERING - Fast processing with proper WebM muxing
 * 
 * Requires: mp4-muxer library (loaded via HTML script tag)
 * https://github.com/Vanilagy/mp4-muxer
 */
'use strict';
import { settings } from '@pixi/settings';
import { SCALE_MODES } from '@pixi/constants';
import { Renderer, BatchRenderer, autoDetectRenderer } from '@pixi/core';
import { Prepare } from '@pixi/prepare';
import { Container } from '@pixi/display';
import { Loader } from '@pixi/loaders';
import { SpritesheetLoader } from '@pixi/spritesheet';
import { CanvasRenderer } from '@pixi/canvas-renderer';
import { CanvasSpriteRenderer } from '@pixi/canvas-sprite';
import { CanvasPrepare } from '@pixi/canvas-prepare';
import '@pixi/canvas-display';
import { ASSETS_PATH } from '../offline_version_js/assets_path.js';
import { PikachuVolleyballReplay } from './pikavolley_replay.js';
import { setGetSpeechBubbleNeeded, enableChat } from '../chat_display.js';
import { serialize } from '../utils/serialize.js';
import { getHashCode } from '../utils/hash_code.js';
import '../../style.css';

console.log('🎬 [INIT] Offline Rendering Mode with MP4 muxing');

class ReplayToVideoConverter {
  constructor() {
    console.log('🎬 [CONSTRUCTOR] Creating converter (Offline Mode)');
    
    Renderer.registerPlugin('prepare', Prepare);
    Renderer.registerPlugin('batch', BatchRenderer);
    CanvasRenderer.registerPlugin('prepare', CanvasPrepare);
    CanvasRenderer.registerPlugin('sprite', CanvasSpriteRenderer);
    Loader.registerPlugin(SpritesheetLoader);
    settings.RESOLUTION = 2;
    settings.SCALE_MODE = SCALE_MODES.NEAREST;
    settings.ROUND_PIXELS = true;

    this.renderer = null;
    this.stage = null;
    this.loader = null;
    this.pikaVolley = null;
    this.currentPack = null;
    this.muxer = null;
    this.videoEncoder = null;
    
    console.log('✅ [CONSTRUCTOR] Created');
  }

  updateStatus(message) {
    const statusEl = document.getElementById('status');
    console.log(`📝 [STATUS] ${message}`);
    if (statusEl) statusEl.textContent = message;
  }

  updateProgress(current, total) {
    const progress = Math.floor((current / total) * 100);
    this.updateStatus(`처리 중... ${progress}% (${current}/${total} 프레임)`);
    
    const progressBar = document.getElementById('progress-bar');
    if (progressBar) progressBar.style.width = `${progress}%`;
  }

  handleRecordButtonClick() {
    console.log('🔘 [BUTTON] Opening file picker...');
    const fileInput = document.createElement('input');
    fileInput.type = 'file';
    fileInput.accept = '.txt';
    fileInput.onchange = (e) => {
      //@ts-ignore
      const file = e.target.files[0];
      if (file) this.loadReplayFile(file);
    };
    fileInput.click();
  }

  loadReplayFile(file) {
    console.log('📥 [LOAD] Loading:', file.name);
    this.updateStatus('리플레이 파일 로딩 중...');
    
    const recordBtn = document.getElementById('record-btn');
    //@ts-ignore
    if (recordBtn) recordBtn.disabled = true;

    const loadingOverlay = document.getElementById('loading-overlay');
    if (loadingOverlay) loadingOverlay.classList.remove('hidden');

    const reader = new FileReader();
    reader.onload = (event) => {
      try {
        //@ts-ignore
        const packWithComment = JSON.parse(event.target.result);
        const pack = packWithComment.pack;
        
        const hash = pack.hash;
        pack.hash = 0;
        if (hash !== getHashCode(serialize(pack))) {
          throw new Error('파일 무결성 검증 실패');
        }
        
        console.log('✅ [LOAD] Verified. Inputs:', pack.inputs?.length);
        this.initializeRenderer(pack);
      } catch (err) {
        console.error('❌ [LOAD] Error:', err);
        this.updateStatus('파일 로딩 실패: ' + err.message);
        //@ts-ignore
        if (recordBtn) recordBtn.disabled = false;
        if (loadingOverlay) loadingOverlay.classList.add('hidden');
      }
    };
    reader.readAsText(file);
  }

  initializeRenderer(pack) {
    console.log('🎨 [RENDERER] Initializing...');
    this.currentPack = pack;

    this.renderer = autoDetectRenderer({
      width: 432,
      height: 304,
      antialias: false,
      backgroundColor: 0x000000,
      forceCanvas: true,
      preserveDrawingBuffer: true,
    });

    this.stage = new Container();
    this.loader = new Loader();

    const container = document.querySelector('#game-canvas-container');
    container.innerHTML = '';
    container.appendChild(this.renderer.view);

    const TEXTURES = ASSETS_PATH.TEXTURES;
    TEXTURES.WITH_COMPUTER = TEXTURES.WITH_FRIEND;

    this.loader.add(ASSETS_PATH.SPRITE_SHEET);
    for (const prop in ASSETS_PATH.SOUNDS) {
      this.loader.add(ASSETS_PATH.SOUNDS[prop]);
    }

    const progressBar = document.getElementById('progress-bar');
    if (progressBar) {
      this.loader.onProgress.add(() => {
        progressBar.style.width = `${this.loader.progress}%`;
      });
    }

    this.loader.load(() => this.setupGame(pack));
  }

  setupGame(pack) {
    console.log('🎮 [GAME] Setting up...');
    
    // Create DOM elements
    const container = document.getElementById('game-canvas-container');
    this.createDOMElements(container);
    
    // Set nicknames and IPs
    this.setNicknamesAndIPs(pack);
    
    // Create game object
    this.pikaVolley = new PikachuVolleyballReplay(
      this.stage,
      this.loader.resources,
      pack.roomID,
      pack.nicknames,
      pack.partialPublicIPs,
      pack.inputs,
      pack.options,
      pack.chats
    );
    
    // Setup chat
    //@ts-ignore
    setGetSpeechBubbleNeeded(this.pikaVolley);
    this.pikaVolley.willDisplayChat = true;
    
    try {
      enableChat(true);
      console.log('✅ [GAME] Chat enabled');
    } catch (err) {
      console.error('❌ [GAME] Chat error:', err);
    }
    
    // Audio
    this.pikaVolley.audio.turnBGMVolume(false);
    this.pikaVolley.audio.turnSFXVolume(false); // Disable during offline render
    
    // Start rendering
    setTimeout(() => this.startOfflineRendering(), 100);
  }

  createDOMElements(container) {
    if (!document.getElementById('player1-chat-box')) {
      const chatBox1 = document.createElement('div');
      chatBox1.id = 'player1-chat-box';
      chatBox1.className = 'chat-box pre-wrap';
      container.appendChild(chatBox1);
    }
    
    if (!document.getElementById('player2-chat-box')) {
      const chatBox2 = document.createElement('div');
      chatBox2.id = 'player2-chat-box';
      chatBox2.className = 'chat-box pre-wrap';
      container.appendChild(chatBox2);
    }
    
    if (!document.querySelector('.nicknames-container')) {
      const nicknamesContainer = document.createElement('div');
      nicknamesContainer.className = 'nicknames-container';
      
      const leftSide = document.createElement('div');
      leftSide.className = 'nickname-and-partial-ip left';
      const nick1 = document.createElement('span');
      nick1.id = 'player1-nickname';
      nick1.className = 'nickname';
      const ip1 = document.createElement('span');
      ip1.id = 'player1-partial-ip';
      ip1.className = 'partial-ip';
      leftSide.appendChild(nick1);
      leftSide.appendChild(ip1);
      
      const rightSide = document.createElement('div');
      rightSide.className = 'nickname-and-partial-ip right';
      const nick2 = document.createElement('span');
      nick2.id = 'player2-nickname';
      nick2.className = 'nickname';
      const ip2 = document.createElement('span');
      ip2.id = 'player2-partial-ip';
      ip2.className = 'partial-ip';
      rightSide.appendChild(nick2);
      rightSide.appendChild(ip2);
      
      nicknamesContainer.appendChild(leftSide);
      nicknamesContainer.appendChild(rightSide);
      container.appendChild(nicknamesContainer);
    }
    
    ['player1-chat-disabled', 'player2-chat-disabled'].forEach(id => {
      if (!document.getElementById(id)) {
        const elem = document.createElement('div');
        elem.id = id;
        elem.className = 'hidden';
        container.appendChild(elem);
      }
    });
  }

  setNicknamesAndIPs(pack) {
    if (pack.nicknames && pack.nicknames.length === 2) {
      const nick1 = document.getElementById('player1-nickname');
      const nick2 = document.getElementById('player2-nickname');
      if (nick1) nick1.textContent = pack.nicknames[0];
      if (nick2) nick2.textContent = pack.nicknames[1];
      console.log('✅ [DOM] Nicknames set');
    }
    
    if (pack.partialPublicIPs && pack.partialPublicIPs.length === 2) {
      const ip1 = document.getElementById('player1-partial-ip');
      const ip2 = document.getElementById('player2-partial-ip');
      if (ip1) ip1.textContent = pack.partialPublicIPs[0];
      if (ip2) ip2.textContent = pack.partialPublicIPs[1];
      console.log('✅ [DOM] IPs set');
    }
  }

  async startOfflineRendering() {
    console.log('🚀 [OFFLINE] Starting...');
    
    const totalFrames = this.pikaVolley.inputs.length;
    console.log(`📊 [OFFLINE] Total frames: ${totalFrames}`);
    
    if (totalFrames === 0) {
      this.updateStatus('리플레이 데이터가 없습니다');
      return;
    }

    // Check for VideoEncoder support
    if (typeof VideoEncoder === 'undefined') {
      alert('이 브라우저는 WebCodecs를 지원하지 않습니다.\nChrome 94+ 또는 Edge 94+를 사용해주세요.');
      this.updateStatus('WebCodecs 미지원');
      return;
    }

    // Check for Muxer library
    //@ts-ignore
    if (typeof Mp4Muxer === 'undefined' && typeof window.WebmMuxer === 'undefined') {
      alert('Muxer 라이브러리가 로드되지 않았습니다.\nHTML에서 mp4-muxer 또는 webm-muxer를 로드해주세요.');
      this.updateStatus('Muxer 라이브러리 없음');
      return;
    }

    try {
      await this.renderWithWebCodecs();
    } catch (err) {
      console.error('❌ [OFFLINE] Error:', err);
      this.updateStatus('렌더링 실패: ' + err.message);
    }
  }

  async renderWithWebCodecs() {
    const fps = 30;
    const width = 432;
    const height = 304;
    const totalFrames = this.pikaVolley.inputs.length;
    
    // Initialize muxer
    //@ts-ignore
    this.muxer = new Mp4Muxer.Muxer({
      //@ts-ignore
      target: new Mp4Muxer.ArrayBufferTarget(),
      video: {
        codec: 'avc',
        width: width,
        height: height,
      },
      fastStart: 'in-memory',
    });
    
    // Initialize encoder
    let resolveEncoderReady;
    const encoderReady = new Promise(resolve => { resolveEncoderReady = resolve; });
    
    this.videoEncoder = new VideoEncoder({
      output: (chunk, meta) => {
        this.muxer.addVideoChunk(chunk, meta);
      },
      error: (err) => {
        console.error('❌ [ENCODER] Error:', err);
        throw err;
      }
    });
    
    this.videoEncoder.configure({
      codec: 'avc1.42001f', // H.264 Baseline
      width: width,
      height: height,
      bitrate: 2_500_000,
      framerate: fps,
      latencyMode: 'quality',
    });
    //@ts-ignore
    resolveEncoderReady();
    await encoderReady;
    
    console.log('✅ [ENCODER] Configured');
    this.updateStatus('프레임 렌더링 및 인코딩 중...');
    
    // Render all frames
    for (let i = 0; i < totalFrames; i++) {
      try {
        this.pikaVolley.gameLoopSilent();
      } catch (err) {
        // Ignore individual frame errors
      }
      
      this.renderer.render(this.stage);
      this.drawOverlaysOnCanvas();
      
      // Capture and encode
      const imageData = this.captureFrameAsImageData();
      const imageBitmap = await createImageBitmap(imageData);
      
      const videoFrame = new VideoFrame(imageBitmap, {
        timestamp: (i * 1_000_000) / fps,
      });
      
      const keyFrame = i % 150 === 0;
      this.videoEncoder.encode(videoFrame, { keyFrame });
      videoFrame.close();
      
      // Progress update
      if (i % 30 === 0 || i === totalFrames - 1) {
        this.updateProgress(i + 1, totalFrames);
        await this.sleep(0);
      }
    }
    
    console.log('✅ [RENDER] All frames encoded');
    this.updateStatus('인코딩 완료 대기 중...');
    
    // Finalize
    await this.videoEncoder.flush();
    this.muxer.finalize();
    
    const buffer = this.muxer.target.buffer;
    console.log(`✅ [MUX] Video created: ${buffer.byteLength} bytes`);
    
    const blob = new Blob([buffer], { type: 'video/mp4' });
    this.downloadVideo(blob);
  }

  captureFrameAsImageData() {
    const canvas = this.renderer.view;
    const ctx = canvas.getContext('2d');
    return ctx.getImageData(0, 0, canvas.width, canvas.height);
  }

  drawOverlaysOnCanvas() {
    const ctx = this.renderer.view.getContext('2d');
    if (!ctx) return;

    ctx.save();
    
    const p1Nick = document.getElementById('player1-nickname')?.textContent || '';
    const p1IP = document.getElementById('player1-partial-ip')?.textContent || '';
    const p2Nick = document.getElementById('player2-nickname')?.textContent || '';
    const p2IP = document.getElementById('player2-partial-ip')?.textContent || '';

    // Player 1
    if (p1Nick) {
      ctx.font = 'bold 16px "Segoe UI", sans-serif';
      ctx.textAlign = 'center';
      ctx.textBaseline = 'top';
      ctx.fillStyle = 'white';
      ctx.strokeStyle = 'black';
      ctx.lineWidth = 3;
      ctx.strokeText(p1Nick, 50, 10);
      ctx.fillText(p1Nick, 50, 10);
      
      if (p1IP) {
        ctx.font = '12px sans-serif';
        ctx.strokeText(p1IP, 50, 30);
        ctx.fillText(p1IP, 50, 30);
      }
    }

    // Player 2
    if (p2Nick) {
      ctx.font = 'bold 16px "Segoe UI", sans-serif';
      ctx.textAlign = 'center';
      ctx.strokeText(p2Nick, 382, 10);
      ctx.fillText(p2Nick, 382, 10);
      
      if (p2IP) {
        ctx.font = '12px sans-serif';
        ctx.strokeText(p2IP, 382, 30);
        ctx.fillText(p2IP, 382, 30);
      }
    }

    // Chat
    this.drawChatBubble(ctx, 'player1-chat-box', 80, 60);
    this.drawChatBubble(ctx, 'player2-chat-box', 352, 60);

    ctx.restore();
  }

  drawChatBubble(ctx, elementId, x, y) {
    const el = document.getElementById(elementId);
    if (!el || !el.textContent || el.textContent.trim() === '') return;

    const text = el.textContent;
    ctx.font = '14px sans-serif';
    ctx.textAlign = 'center';
    
    const textMetrics = ctx.measureText(text);
    const width = textMetrics.width + 20;
    const height = 30;

    ctx.fillStyle = 'rgba(255, 255, 255, 0.9)';
    ctx.fillRect(x - width / 2, y, width, height);
    ctx.strokeStyle = 'black';
    ctx.lineWidth = 1;
    ctx.strokeRect(x - width / 2, y, width, height);

    ctx.fillStyle = 'black';
    ctx.fillText(text, x, y + 10);
  }

  downloadVideo(blob) {
    console.log('💾 [DOWNLOAD] Downloading...');
    this.updateStatus('동영상 다운로드 중...');
    
    const url = URL.createObjectURL(blob);
    const d = new Date();
    const dateStr = `${d.getFullYear()}${String(d.getMonth() + 1).padStart(2, '0')}${String(d.getDate()).padStart(2, '0')}`;
    const timeStr = `${String(d.getHours()).padStart(2, '0')}${String(d.getMinutes()).padStart(2, '0')}`;
    
    let filename = `${dateStr}_${timeStr}_pikavolley.mp4`;
    
    if (this.currentPack?.nicknames?.length === 2) {
      const nick1 = this.currentPack.nicknames[0].replace(/[/\\?%*:|"<>]/g, '_');
      const nick2 = this.currentPack.nicknames[1].replace(/[/\\?%*:|"<>]/g, '_');
      filename = `${dateStr}_${timeStr}_${nick1}_vs_${nick2}.mp4`;
    }
    
    const a = document.createElement('a');
    a.href = url;
    a.download = filename;
    a.click();
    
    setTimeout(() => URL.revokeObjectURL(url), 100);

    this.updateStatus('완료! 동영상이 다운로드되었습니다.');
    
    const recordBtn = document.getElementById('record-btn');
    //@ts-ignore
    if (recordBtn) recordBtn.disabled = false;
    
    const loadingOverlay = document.getElementById('loading-overlay');
    if (loadingOverlay) loadingOverlay.classList.add('hidden');
    
    console.log('✅ [COMPLETE]');
  }

  sleep(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
  }
}

// Adjust assets path
function adjustAssetsPath() {
  ASSETS_PATH.SPRITE_SHEET = '../' + ASSETS_PATH.SPRITE_SHEET;
  for (const prop in ASSETS_PATH.SOUNDS) {
    ASSETS_PATH.SOUNDS[prop] = '../' + ASSETS_PATH.SOUNDS[prop];
  }
}

adjustAssetsPath();

// Initialize
const converter = new ReplayToVideoConverter();
const recordBtn = document.getElementById('record-btn');
if (recordBtn) {
  recordBtn.addEventListener('click', () => converter.handleRecordButtonClick());
}

console.log('✅ [INIT] Offline rendering ready');
